var RouletteData = {
    ALLROATE: 360,//360
    num: 8, // 
    deviation: 5, // ()
    offset: 22.5, // 
    //
    zhuanpanData: {
        default: {}
    },

    duration: 3, //
    rotateNum: 3, //(n - 1)

    load: function () {
        for (let i = 1; i < 9; i++) {
            this.zhuanpanData[i] = {
                start: (this.num - (9 - i)) * this.ALLROATE / this.num - this.offset + this.deviation, end: (this.num - (8 - i)) * this.ALLROATE / this.num - this.offset - this.deviation
            }
        }
    }
};
RouletteData.load();

module.exports = RouletteData;
